
.step-progress__wrapper {
  width: 90%;
  margin: 0 auto;
  position: relative;
}
.step-progress__wrapper-before {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  height: 12px;
  width: 100%;
  background-color: gray;
  transform: translateY(-50%) perspective(1000px);
}
.step-progress__wrapper-after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  height: 12px;
  width: 100%;
  background-color: red;
  transform: scaleX(0) translateY(-50%) perspective(1000px);
  transform-origin: left center;
  transition: transform .5s ease;
}
.step-progress__bar {
  width: 100%;
  display: flex;
  height: 100px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}
.step-progress__step {
  z-index: 2;
  position: relative;
}
.step-progress__step span {
    color: gray;
    transition: .3s ease;
    display: block;
    font-size: 50px;
    transform: translate3d(0, 0, 0) scale(1) perspective(1000px);
    font-weight: 900;
    text-align: center;
    opacity: 1;
}
@media (max-width: 767px) {
.step-progress__step span {
        font-size: 28px;
}
}
.step-progress__step--active span {
    color: red;
}
.step-progress__step--active .step-progress__step-label {
    color: red;
}
.step-progress__step--active .step-progress__step-icon {
    opacity: 1;
}
.step-progress__step--valid .step-progress__step-icon {
    opacity: 1;
    transform: translate3d(-50%, -50%, 0) scale(1) perspective(1000px);
}
.step-progress__step--valid span {
    color: red;
    opacity: 0;
    transform: translate3d(0, 0, 0) scale(2) perspective(1000px);
}
.step-progress__step--valid .step-progress__step-label {
    color: red;
}
.step-progress__step:after {
    content: "";
    position: absolute;
    z-index: -1;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) perspective(1000px);
    width: 75px;
    height: 75px;
    background-color: #fff;
    border-radius: 50%;
    border: 5px solid gray;
    transition: .3s ease;
}
@media (max-width: 767px) {
.step-progress__step:after {
        width: 40px;
        height: 40px;
}
}
.step-progress__step--active:after {
    border: 5px solid red;
}
.step-progress__step--valid:after {
    background-color: red;
    border: 5px solid red;
}
.step-progress__step-label {
  position: absolute;
  top: calc(100% + 25px);
  left: 50%;
  transform: translateX(-50%) perspective(1000px);
  white-space: nowrap;
  font-size: 18px;
  font-weight: 600;
  color: gray;
  transition: .3s ease;
}
.step-progress__step-icon {
  font-size: 36px;
  color: #fff;
  position: absolute;
  left: 50%;
  top: 50%;
  transition: transform .3s ease;
  opacity: 0;
  transform: translate3d(-50%, -50%, 0) scale(0) perspective(1000px);
}
@media (max-width: 767px) {
.step-progress__step-icon {
      font-size: 22px;
}
}
